<template>
    <div>
        
        <header id="header" :class="{ fixed: hasFixed }">
            <div class="container">
                <nav class="nav">
                    <div class="header_logo">
                        <img src="../assets/img/logo.svg" alt="">
                    </div>
                    <ul class="menu_list">
                        <li>
                        <router-link class="router-link-exact-active" to="/">About</router-link> 
                        </li>
                        <li>
                        <router-link to="/products">Products</router-link> 
                        </li>
                        <li>
                        <router-link to="/ingredients">Ingredients</router-link> 
                        </li>
                        <li>
                        <router-link to="/howitworks">How it works</router-link> 
                        </li>
                        <li>
                        <router-link to="/blog">Blog</router-link> 
                        </li>
                    </ul>
                    
                        <button @click="toggleMenu" class="hamburger hamburger--slider" type="button" :class="isMenu?'is-active':''">
                            <span class="hamburger-box">
                            <span class="hamburger-inner"></span>
                            </span>
                        </button>
                    
                </nav>
            </div>
            
        </header>
    </div>
</template>
<style scoped>
@import url('../assets/css/css/hamburgers.css');
 @import url('../assets/css/css/style.css');
    li {
        cursor: pointer !important;
    }
    .focus {
        color: #E38C00 !important; 
    }
    .hamburger--slider .hamburger-inner {
    top: -9px;
}
    
</style>

<script >
import store from '@/store/index.js';
export default ({
    
    name: 'siteHeader',
    data() {
        return {
        hasFixed: false,
        isMenu:store.getters.menu
        };
    },
    mounted() {
        window.addEventListener("scroll", this.scroll_header);
    },
    watch: {
        '$store.state.mobileMenu': function(){
            this.isMenu = store.state.mobileMenu;
        }
    },
    methods: {
        scroll_header() {
            if (window.scrollY > 100) {
                this.hasFixed = true;
            } else {
                this.hasFixed = false;
            }
        },
        toggleMenu(){
            store.commit('toggleMenu', true);
            this.isMenu = true;
        }
    },
  



})
    

</script>
